@font-face {
    font-family: Din;
    src: url(/static/fonts/DIN\ Medium.ttf);
  }

body{
    background-image: url("/static/images/ChelseaHomePage.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    background-size:cover;
    margin: 0;
    padding: 0;
}

.arrows{
    color: whitesmoke;
    font-size: 100%;
    position: absolute;
    top: 50%;
    left: 90%;
}

.arrows ion-icon {
    margin: 0 8px;
    border: 2px solid white;
    padding: 12px;
    transition: background-color 0.3s;
}

.arrows ion-icon:hover{
    background-color: blue;
}

.nav_bar{
    display: flex;
    flex-direction: row;
    background-color: rgba(255, 255, 255, 0.85); 
    width: 50%;
    justify-content: center;
    position: relative;
    position: absolute;
    top: 0;
    left: 25%;
   align-items: center;
}

.nav_bar a{
    display: flex;
    flex-direction: row;
    position: relative;
    margin-left: 9%;
    margin-right: 15px;
    text-decoration: none;
    font-family: Din;
    color: gray;
    transition: 0.3s ease-in;
}

.nav_bar a:hover{
    color: blue;
    text-decoration: solid;
    transition: 0.3s ease-in;
}


.nav_bar ion-icon{
    position: absolute;
    margin-left: -88%;
}

.nav_bar img{
    position: relative;
    margin-left: -7%;
    margin-right: -7%;
}

.nav_bar .search{
    position: absolute;
    margin-left: 89%;
}

